* {
  margin: 0;
  font-family: Helvetica, sans-serif;
  box-sizing: border-box;
}

body{
  display: flex;  
  justify-content: center;
  
}
ol{
  padding: 0;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color: grey;
  margin-bottom: 10px;
}
.card-grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-bottom: 100px;
  gap: 10px;
  

}
.card{
  width: 300px;
  height: 300px;
  overflow: hidden;
  object-fit: cover;

}

.card img {
  width: 100%;
  height: 100%;
}


